.profile__header {
  margin: 0;
  padding: 0;
  width: 294px;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-align: left;
  color: #FFFFFF;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}


@media screen and (max-width: 600px) {
  .profile__header {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    max-width: 189px;


  }

}





